import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Row, Col, DatePicker, Typography, message } from 'antd';
import dayjs from 'dayjs';

const { Title } = Typography;

const Register = () => {
    const [form] = Form.useForm();
    const [referrer, setReferrer] = useState('');

    useEffect(() => {
        document.title = "ng K Ti Khon"; // Cp nht tiu  ca trang

        const hash = window.location.hash;
        const urlParams = new URLSearchParams(hash.split('?')[1]);
        const ref = urlParams.get('ref');
        if (ref) {
            setReferrer(ref);
            form.setFieldsValue({ referrer: ref });
        }
    }, [form]);

    const onFinish = async (values) => {
        // nh dng li ngy thng nm sinh trc khi gi i
        const formattedValues = {
            ...values,
            birthdate: values.birthdate ? dayjs(values.birthdate).format('DD/MM/YYYY') : '',
        };

        try {
            const response = await fetch('/api/register', { // Gi s y l API ca bn
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(formattedValues),
            });

            const result = await response.json();

            if (response.ok) {
                message.success('ng k thnh cng!');
            } else {
                message.error(`ng k tht bi: ${result.message}`);
            }
        } catch (error) {
            message.error(`C li xy ra: ${error.message}`);
        }
    };

    return (
        <Row justify="center" style={{ marginTop: '50px' }}>
            <Col xs={24} sm={20} md={16} lg={12} xl={8}>
                <Title level={2} style={{ textAlign: 'center', marginBottom: '20px' }}>
                    ng K Ti Khon
                </Title>
                <Form
                    form={form}
                    name="register"
                    onFinish={onFinish}
                    layout="vertical"
                    size="large"
                >
                    <Form.Item label="Ngi gii thiu" name="referrer">
                        <Input disabled />
                    </Form.Item>
                    <Form.Item
                        label="H v tn"
                        name="fullName"
                        rules={[{ required: true, message: 'Vui lng nhp h v tn!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="S in thoi"
                        name="phone"
                        rules={[{ required: true, message: 'Vui lng nhp s in thoi!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="Email"
                        name="email"
                        rules={[{ required: true, type: 'email', message: 'Vui lng nhp email hp l!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="Mt khu"
                        name="password"
                        rules={[{ required: true, message: 'Vui lng nhp mt khu!' }]}
                    >
                        <Input.Password />
                    </Form.Item>
                    <Form.Item
                        label="Nhp li mt khu"
                        name="confirmPassword"
                        dependencies={['password']}
                        rules={[
                            { required: true, message: 'Vui lng nhp li mt khu!' },
                            ({ getFieldValue }) => ({
                                validator(_, value) {
                                    if (!value || getFieldValue('password') === value) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject('Mt khu khng khp!');
                                },
                            }),
                        ]}
                    >
                        <Input.Password />
                    </Form.Item>
                    <Form.Item
                        label="Ngy thng nm sinh"
                        name="birthdate"
                        rules={[{ required: true, message: 'Vui lng chn ngy thng nm sinh!' }]}
                    >
                        <DatePicker format="DD/MM/YYYY" style={{ width: '100%' }} />
                    </Form.Item>
                    <Form.Item
                        label="S CMND hoc CCCD"
                        name="identityNumber"
                        rules={[{ required: true, message: 'Vui lng nhp s CMND hoc CCCD!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="S ti khon ngn hng"
                        name="bankAccountNumber"
                        rules={[{ required: true, message: 'Vui lng nhp s ti khon ngn hng!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="Tn ch ti khon"
                        name="accountHolderName"
                        rules={[{ required: true, message: 'Vui lng nhp tn ch ti khon!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="Tn ngn hng"
                        name="bankName"
                        rules={[{ required: true, message: 'Vui lng nhp tn ngn hng!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="Chi nhnh ngn hng"
                        name="bankBranch"
                        rules={[{ required: true, message: 'Vui lng nhp chi nhnh ngn hng!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit" block>
                            ng k
                        </Button>
                    </Form.Item>
                </Form>
            </Col>
        </Row>
    );
};

export default Register;
